<template>
  <div class="store" id="store_page" style="padding-top: 44px;">
    <back-header>
      <router-link class="open-store" to="/personal">
        <van-icon name="shop-o" class="shop-icon" />
        <span>我要开店</span>
      </router-link>
    </back-header>

    <scroll class="scroll" ref="scroll" @scrollToEnd="onLoad" :is-finished="isFinished" clickable>
      <div>
        <div class="boss">
          <img class="avatar" :src="user.avatar || $store.state.defaultAvatar" alt="">
          <p>{{ user.nick_name }}</p>
          <div style="flex: 1"></div>
          <img @click="chat" class="chat" src="../goods/kefu@3x.png" alt="">
        </div>

        <div class="goods-list">
          <h4>
            <span>{{ goodsType }}（{{ goodsCount }}）</span>
<!--            <img @click="showClassify=true" src="./fenlei@3x.png" alt="">-->
          </h4>
          <div class="list">
            <goods-item v-for="item in goods" :goods="item" :key="item.id" />
          </div>
        </div>
      </div>
    </scroll>
    <goods-classify @close="close" :show="showClassify" v-show="showClassify" />
  </div>
</template>

<script>
import BackHeader from '../../components/back-header/back-header'
import GoodsItem from '../../components/goods-item/goods-item'
import GoodsClassify from '../../components/goods-classify/goods-classify'
import Scroll from '../../components/scroll/scroll'
import { post } from '../../common/js/http'

const PAGE_SIZE = 10

export default {
  name: 'store',
  components: {
    BackHeader,
    GoodsItem,
    GoodsClassify,
    Scroll
  },
  data() {
    return {
      goods: [],
      loading: false,
      showClassify: false,
      goodsType: '全部商品',
      page: 1,
      isFinished: true,
      goodsCount: 0,
      user: {},
      shopid: ''
    }
  },
  mounted() {
    this.shopid = this.$route.params.id
    this.getList()
  },
  activated() {
    if (this.$route.params.id !== this.shopid) {
      this.shopid = this.$route.params.id
      this.getList()
    }
    this.$refs.scroll.refresh()
  },
  methods: {
    chat() {
      this.$jsCallNative('chat', {
        uid: this.user.id + '',
        nick_name: this.user.nick_name
      })
    },
    onLoad() {
      if (this.loading) return
      this.loading = true
      this.page++
      this.getList()
    },
    close(item) {
      if (item) {
        this.goodsType = item
      }
      this.showClassify = false
    },
    getList() {
      let loading = null
      if (this.page === 1) {
        loading = this.$skeLoading('#store_page')
      }
      post('/shop/goods/getGoodsList', {
        shopid: this.shopid,
        page: this.page,
        size: PAGE_SIZE
      })
        .then(({ data: { data } }) => {
          this.goods = this.goods.concat(data.list)
          this.isFinished = data.list.length < PAGE_SIZE
          this.goodsCount = data.count
          this.user = data.shop.user
          this.loading = false
          loading && loading.close()
          this.$nextTick(() => {
            this.$refs.scroll.refresh()
          })
        })
        .catch(() => {
          loading && loading.close()
        })
    }
  }
}
</script>

